* {
    box-sizing: border-box;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    color: #fff;
    padding: 10px;
    width: 100%;
    height: 3.5rem;
    font-size: 1.5rem;
  }
  
  .left-links {
    display: flex;
    align-items: center;
  }
  
  .left-links a {
    text-decoration: none;
    color: white;
    margin-right: 20px;
  }
  
  .right-links {
    display: flex;
    align-items: center;
  }

  
  